4px;\n  border: 2px solid gray;\n  padding: 0.25rem;\n}\n\n.clearing {\n  clear:both;\n}\n\n.buttons {\n  margin: 1rem auto;\n  width: 20vw;\n  clear:both;\n}\n\n.auto {\n  overflow:auto;\n}\n\n.medium-m {\n  margin: 1rem 0;\n}\n</style>\n"],"sourceRoot":""}]);

